import { Component, OnInit } from '@angular/core';
import { KalenderCutiPengaturanPerpegawaiService, KalenderCutiPengaturanService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Cuti_pengaturan_perpegawai, Cuti_pengaturan, Pegawai } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private kalenderCutiPengaturanPerpegawaiService: KalenderCutiPengaturanPerpegawaiService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
        private activatedRoute: ActivatedRoute,
        private pesan: PesanService,
        private router: Router,

    ) { }

    id_pegawai: string = ''
    dataPengaturanPerpegawai: Cuti_pengaturan_perpegawai = {}

    id_cuti_pengaturan_perpegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_cuti_pengaturan_perpegawai')) {
                this.id_cuti_pengaturan_perpegawai = data.get('id_cuti_pengaturan_perpegawai')
                console.log(data.get('id_cuti_pengaturan_perpegawai'))

                this.getPreeData()
                this.form()
                this.getData()
            }
        })
    }

    listCutiPengaturan: Cuti_pengaturan[] = []
    listPegawai: Pegawai[] = []
    getPreeData() {
        this.kalenderCutiPengaturanService.getKalenderCutiPengaturans().subscribe(data => {
            this.listCutiPengaturan = data.data
        })
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data
            }
        )
    }

    getData() {
        this.spinner.show()
        this.kalenderCutiPengaturanPerpegawaiService.getKalenderCutiPengaturanPerpegawaisId(this.id_cuti_pengaturan_perpegawai).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Cuti_pengaturan_perpegawai>
    form() {
        this.dataForm = this.fb.group<Cuti_pengaturan_perpegawai>({
            id_cuti_pengaturan: new FormControl('', [Validators.required]),
            approval_atasan: new FormControl('', [Validators.required]),
            approval_end: new FormControl('', [Validators.required]),
            saldo_custom: new FormControl(''),
            tanggal_mulai_berlaku: new FormControl(''),
            status_pengaturan: new FormControl(''),
            id_cuti_pengaturan_perpegawai: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPengaturanPerpegawai = this.dataForm.value
        this.kalenderCutiPengaturanPerpegawaiService
            .putKalenderCutiPengaturanPerpegawaisId({ id: this.id_cuti_pengaturan_perpegawai, body: this.dataPengaturanPerpegawai })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan-cuti', 'list'])
    }

}
