import { Component, OnInit } from '@angular/core';
import { KalenderCutiPengaturanPerpegawaiService } from 'src/app/sdk/hr/services/kalender-cuti-pengaturan-perpegawai.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Pegawai_pendidikan } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private kalenderCutiPengaturanPerpegawaiService: KalenderCutiPengaturanPerpegawaiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataPengaturanCutiPegawai: Pegawai_pendidikan[] = []
    id_pengaturan_cuti_perpegawai: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getCutiPerpegawai()
    }


    getCutiPerpegawai() {
        this.spinner.show()
        this.kalenderCutiPengaturanPerpegawaiService.getKalenderCutiPengaturanPerpegawais(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPengaturanCutiPegawai = data.data
                this.spinner.hide()
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.kalenderCutiPengaturanPerpegawaiService
            .deleteKalenderCutiPengaturanPerpegawaisId(id)
            .subscribe(data => {
                this.getCutiPerpegawai()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan-cuti', 'list'])
    }

}
